import axios from "axios";

function downloadPDF(url, name) {
    return axios.get(
        url,
        {
            'authCall': true,
            responseType: 'blob'
        })
        .then((response) => {
            const anchor = document.createElement('a');
            anchor.href = window.URL.createObjectURL(new Blob([response.data]));
            anchor.target = '_blank';
            anchor.download = `${name}.pdf`;
            anchor.click();

            return Promise.resolve();
        })
        .catch((err) => {
            return Promise.reject(err);
        });
}

export function downloadInvoice (id) {
    return downloadPDF(`activities/api/${id}/invoice_pdf/`, 'factuur');
}

export function downloadQuote (id) {
    return downloadPDF(`activities/api/${id}/quote_pdf/`, 'offerte');
}

export function downloadActivityMap (id) {
    return downloadPDF(`activities/api/${id}/activity_map_pdf/`, 'activiteitsmap');
}