                @selected="toggleDate"
                weekday-header-format="long"
            ></b-calendar>
          </b-col>
        </b-row>
        <hr />
        <b-row>
          <b-col>
            <b-form>
              <b-form-row>
                <b-col sm="2">
                  <b-form-group
                      id="input-group-add-weekends"
