<script>
import {mapActions, mapGetters} from "vuex";
import {clearTokens} from "@/utils/authTokenManager";

export default {
  name: "UserNavbar",
  props: {

  },
  methods: {
    ...mapActions(['logout']),
    logoutUser() {
      this.logout().then(() => {
        clearTokens();
        if (this.$route.meta?.requiresAuth) {
          this.$router.push({name: 'Home'})
        }
        this.$bvToast.toast(`U bent uitgelogd`, {
          title: 'Uitgelogd!',
          autoHideDelay: 1500,
          variant: 'success',
          appendToast: true
        });
      })
    },
  },
  computed: {
    ...mapGetters(["getLoginState", "getUserBody"]),
    isStaff() {
      return this.getUserBody?.isStaff || this.getUserBody?.isAdmin;
    },
    isAdmin() {
      return this.getUserBody?.isAdmin;
    },
    hasPersonnelProfile() {
      return this.getUserBody?.relatedPersonnel;
    },
    hasClientProfile() {
      return this.getUserBody?.relatedClient;
    }
  }
}
</script>

