                  :label-for="`input-member-${member.id}-is_tryout`"
                  description="Let er op dat er een ervaren Rino'er/cordinator op de activiteit meegaat! Gebruik deze optie niet voor rijder of cordinator try-outs (tenzij de Rino'er dan k een rijder/coord try-out heeft)"
              >
                <b-form-checkbox
                    :id="`input-member-${member.id}-is_tryout`"
                    v-model="member.is_tryout"
                    switch
                ></b-form-checkbox>
              </b-form-group>
            </b-col>
            <b-col sm="4">
              <b-form-group
                  :id="`input-group-member-${member.id}-is_driver_tryout`"
                  label="Gaat op rijder try-out:"
                  :label-for="`input-member-${member.id}-is_driver_tryout`"
                  description="Let er op dat er ook nog een volwaardige rijder op de activiteit meegaat!"
              >
                <b-form-checkbox
                    :id="`input-member-${member.id}-is_driver_tryout`"
                    v-model="member.is_driver_tryout"
                    switch
                ></b-form-checkbox>
              </b-form-group>
            </b-col>
            <b-col sm="4">
              <b-form-group
                  :id="`input-group-member-${member.id}-is_coordinator_tryout`"
                  label="Gaat op cordinator try-out:"
                  :label-for="`input-member-${member.id}-is_coordinator_tryout`"
                  description="Let er op dat je naast deze try-out cordinator ook nog een ervaren cordinator op de activiteit zet!"
              >
                <b-form-checkbox
                    :id="`input-member-${member.id}-is_coordinator_tryout`"
                    v-model="member.is_coordinator_tryout"
                    switch
                ></b-form-checkbox>
              </b-form-group>
            </b-col>
          </b-form-row>
          <b-form-row>
            <b-col>
              <a class="text-danger" @click="removePersonnel(member.id)">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-x-fill" viewBox="0 0 16 16">
                  <path fill-rule="evenodd" d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm6.146-2.854a.5.5 0 0 1 .708 0L14 6.293l1.146-1.147a.5.5 0 0 1 .708.708L14.707 7l1.147 1.146a.5.5 0 0 1-.708.708L14 7.707l-1.146 1.147a.5.5 0 0 1-.708-.708L13.293 7l-1.147-1.146a.5.5 0 0 1 0-.708z"/>
                </svg>
                Haal Rino'er van activiteit af
              </a>
