<template>
  <b-container>
    <b-row>
      <b-col>
        <line-chart :chart-data="datacollection"></line-chart>
        <button @click="fillCollection()">Randomize</button>
      </b-col>
      <b-col>
        <line-chart :chart-data="datacollection1"></line-chart>
        <button @click="fillCollection()">Randomize</button>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <pie-chart :chart-data="datacollection3"></pie-chart>
      </b-col>
      <b-col>
        <bar-chart :chart-data="datacollection2"></bar-chart>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <horizontal-bar-chart :chart-data="datacollection4" ></horizontal-bar-chart>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import LineChart from "@/components/Statistics/ChartTypes/LineChart";
import BarChart from "@/components/Statistics/ChartTypes/BarChart";
import PieChart from "@/components/Statistics/ChartTypes/PieChart";
import HorizontalBarChart from "@/components/Statistics/ChartTypes/HorizontalBarChart";

export default {
  name: "Statistics_example",
  components: {
    LineChart,
    BarChart,
    PieChart,
    HorizontalBarChart
  },
  data () {
    return {
      datacollection2: {
        labels: ["week 1", "week 2", "week 3", "week 4", "week 5", "week 6", "week 7", "week 8", "week 9", "week 10"],
        datasets: [
          {
            data: [86, 114, 106, 106, 107, 111, 133, 221, 783, 2478],
            label: "Africa",
            backgroundColor: "#3e95cd",
            borderColor: "#3e95cd",
            fill: false
          },
          {
            data: [282, 350, 411, 502, 635, 809, 947, 1402, 3700, 5267],
            label: "Asia",
            backgroundColor: "#8e5ea2",
            borderColor: "#8e5ea2",
            fill: false
          },
          {
            data: [168, 170, 178, 190, 203, 276, 408, 547, 675, 734],
            label: "Europe",
            backgroundColor: "#3cba9f",
            borderColor: "#3cba9f",
            fill: false
          },
          {
            data: [40, 20, 10, 16, 24, 38, 74, 167, 508, 784],
            label: "Latin America",
            backgroundColor: "#e8c3b9",
            borderColor: "#e8c3b9",
            fill: false
          },
          {
            data: [6, 3, 2, 2, 7, 26, 82, 172, 312, 433],
            label: "North America",
            backgroundColor: "#c45850",
            borderColor: "#c45850",
            fill: false
          }
        ]
      },

      datacollection: null,
      datacollection1: null,

      datacollection3: {
        labels: ["Bestuur", "Niet bestuur"],
        datasets: [
          {
            data: [86, 14],
            backgroundColor: [
              "#3e95cd",
              "#c45850",
            ],
            borderColor: [
              "#3e95cd",
              "#c45850",
            ],
            fill: false
          },
        ]
      },

      datacollection4: {
        labels: ["a", "bb", "ccc", "dddd", "eeeee"],
        datasets: [
          {
            label: 'Dataset 1',
            data: [-2, 4, 9, 10, -3],
            borderColor: "#0033cc",
            backgroundColor: "#0033cc",
          },
          {
            label: 'Dataset 2',
            data: [9, -3, -5, -8, 2],
            borderColor: "#ff0000",
            backgroundColor: "#ff0000",
          }
        ]
      },
    }
  },
  methods: {
    fillData () {
      return {
        labels: [
          this.getRandomInt(),
          this.getRandomInt()
        ],
        datasets: [
          {
            label: 'Data One',
            backgroundColor: '#f87979',
            borderColor: "#f87979",
            data: [this.getRandomInt(), this.getRandomInt()],
            fill: false
          },
          {
            label: 'Data Two',
            backgroundColor: '#2660d9',
            borderColor: "#2660d9",
            data: [this.getRandomInt(), this.getRandomInt()],
            fill: false
          }
        ]
      }
    },
    fillCollection() {
      this.datacollection = this.fillData();
      this.datacollection1 = this.fillData();
    },
    getRandomInt () {
      return Math.floor(Math.random() * (50 - 5 + 1)) + 5
    }
  },
  created () {
    this.fillCollection();
  }
}
</script>

